.ProjectSpace {
    width: 100%;
    text-align: center;
  }
  
  .ProjectSpace > img {
    width: 100%;
    height: auto;
  }

  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    color: rgb(29, 31, 46);
    background-color: #eee;
    margin: 0;
}

h1, h2, h3, p {
    margin: 1rem 0 1rem 0;
}

.wrapper {
    width: 800px;
    margin: auto;
}

/* img {
    width: 100%;
