h1 {
    font-size: 3rem;
    font-weight: lighter;
}

h2 {
    font-size: 2.5rem;
}

h3 {
    font-size: 1.5rem;
    font-weight: 400;
}

.project {
    margin: 4rem 0 4rem 0;
}

.header {
    padding: 1rem;
    background-color: rgb(252, 205, 205);

}

.header > header {
    width: 800px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: baseline
}